<template>
    <div class="admin-index">
        <top-nav :title="'后台首页'"></top-nav>

        <div class="wyu-tips wyu-tips-info">
            <p class="tit">
                注意事项:微信、百度、支付宝、抖音小程序等需要设置以下相关域名
            </p>
            <p>request合法域名：<span class="red">{{request}}</span></p>
            <p>socket合法域名：<span class="red">{{socket}}</span></p>
            <p>uploadFile合法域名：<span class="red">{{uploadFile}}</span></p>
            <p>downloadFile合法域名：<span class="red">{{downloadFile}}</span></p>

            <p class="tit">
                <p> H5支付配置说明:</p>
                <p> 1)H5支付的微信支付参数从微信小程序的支付参数中获得(微信支付中需开通APP支付和H5支付权限)</p>
                <p> 2)H5支付的支付宝支付参数从抖音小程序的支付参数中获得(支付宝支付需开通APP支付和H5支付权限)</p>



        </div>

        <el-row style="margin-top:20px;padding:0 20px;" :gutter="20">
            <el-col :span="6">
                <div class="xcx-type" v-loading="h5Url==''">
                    <qrcode-vue :value="h5Url"></qrcode-vue>
                    <p>使用手机扫码预览H5</p>
                    <div class="urlinfo">
                        <el-input readonly size="small"  style="width: 100px;" :value="h5Url"></el-input>
                        <a style="margin-left:10px;" class="tag-read" @click="copy"  :data-clipboard-text="h5Url" href="javascript:void(0);">点击复制</a>
                    </div>
                </div>
            </el-col>
            <el-col :span="18">
                <div class="xcx-type">
                    <el-row>
                        <el-col :span="12">
                            <img src="@/assets/img/style.png">
                            <p>可视化编辑页面，让您的小程序富有活力 <router-link to="/setting/style"><a href="javascript:void(0);">去制作</a></router-link></p>
                        </el-col>
                        <el-col style="     border-left: 1px solid #f2f2f2;" :span="12">
                            <img src="@/assets/img/manage.png">
                            <p>做小程序需要完善小程序的内容 <router-link to="/product/product/index"><a href="javascript:void(0);">去设置</a></router-link></p>
                        </el-col>
                    </el-row>
                </div>
            </el-col>
        </el-row>

        <el-row style="margin-top:20px;padding:0 20px;" :gutter="20">
            <el-col :span="6">
                <div class="xcx-type">
                    <div class="tit2">
                        <i class="iconfont icon-weixin"></i>
                        <p>微信小程序</p>
                    </div>

                    <div class="btn-list">
                        <el-button size="small" @click="dialogWeixinVisible = true,getWeixin()">基本配置</el-button>
                        <a  style="margin-left:10px;" target="_blank" :href="downloadurl+'&type=mp-weixin'"><el-button  size="small">打包下载</el-button></a>
                    </div>
                    <p>没有小程序?<a target="_blank" href="https://weixin.qq.com">去注册</a></p>
                </div>
            </el-col>
            <el-col :span="6">
                <div class="xcx-type">
                    <div class="tit2">
                        <i style="color:#409EFF;" class="iconfont icon-zhifubao"></i>
                        <p>支付宝小程序</p>
                    </div>

                    <div class="btn-list">
                        <el-button size="small" @click="dialogAliVisible = true,getAli()">基本配置</el-button>
                        <a  style="margin-left:10px;" target="_blank" :href="downloadurl+'&type=mp-alipay'"><el-button  size="small">打包下载</el-button></a>
                    </div>
                    <p>没有小程序 ? <a target="_blank" href="https://open.alipay.com/channel/miniIndex.htm">去注册</a></p>
                </div>
            </el-col>
            <el-col :span="6">
                <div class="xcx-type">
                    <div class="tit2">
                        <i style="color:blue;" class="iconfont icon-baidu"></i>
                        <p>百度小程序</p>
                    </div>

                    <div class="btn-list">
                        <el-button size="small" @click="dialogBaiduVisible=true,getBaidu()">基本配置</el-button>
                        <a  style="margin-left:10px;" target="_blank" :href="downloadurl+'&type=mp-baidu'"><el-button  size="small">打包下载</el-button></a>

                    </div>
                    <p>没有小程序 ? <a target="_blank" href="https://smartprogram.baidu.com/developer/index.html">去注册</a></p>
                </div>
            </el-col>
            <el-col :span="6">
                <div class="xcx-type">
                    <div class="tit2">
                        <i style="color:#1f0f1c;" class="iconfont icon-douyin"></i>
                        <p>抖音小程序（头条）</p>
                    </div>

                    <div class="btn-list">
                        <el-button size="small" @click="dialogToutiaoVisible=true,getToutiao()">基本配置</el-button>
                        <a  style="margin-left:10px;" target="_blank" :href="downloadurl+'&type=mp-toutiao'"><el-button size="small">打包下载</el-button></a>
                    </div>
                    <p>没有小程序?<a target="_blank" href="https://developer.toutiao.com/homepage">去注册</a></p>
                </div>
            </el-col>
        </el-row>



        <el-dialog title="微信小程序登录/支付设置" :visible.sync="dialogWeixinVisible" :modal-append-to-body="false"
                   v-loading="subLoading">
            <el-form :model="weixinform" style="padding: 0" size="mini">
                <el-form-item label="appid" :label-width="formLabelWidth">
                    <el-input v-model="weixinform.appid" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="appsecret" :label-width="formLabelWidth">
                    <el-input v-model="weixinform.appsecret" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="商户ID" :label-width="formLabelWidth">
                    <el-input v-model="weixinform.merchantid" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="商户密钥" :label-width="formLabelWidth">
                    <el-input v-model="weixinform.merchantsecret" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="证书" :label-width="formLabelWidth">
                    <el-input type="textarea" :rows="5" v-model="weixinform.cert" autocomplete="off"></el-input>
                    <p>一般为.cert格式</p>
                </el-form-item>
                <el-form-item label="证书密钥" :label-width="formLabelWidth">
                    <el-input type="textarea" :rows="5" v-model="weixinform.pem" autocomplete="off"></el-input>
                    <p>一般为.pem格式</p>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="mini" @click="dialogWeixinVisible = false">取 消</el-button>
                <el-button size="mini" type="primary" @click="setWeixin()">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="支付宝小程序登录/支付设置" :visible.sync="dialogAliVisible" v-loading="subLoading"
                   :modal-append-to-body="false">
            <el-form :model="aliform" size="mini" style="padding:0;">
                <el-form-item label="appid" :label-width="formLabelWidth">
                    <el-input v-model="aliform.appid" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="公钥" :label-width="formLabelWidth">
                    <el-input type="textarea" :rows="5" v-model="aliform.publickey" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="私钥" :label-width="formLabelWidth">
                    <el-input type="textarea" :rows="5" v-model="aliform.privatekey" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="AES密钥" :label-width="formLabelWidth">
                    <el-input type="textarea" :rows="5" v-model="aliform.aeskey" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="mini" @click="dialogAliVisible = false">取 消</el-button>
                <el-button size="mini" type="primary" @click="setAli()">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="百度小程序登录/支付设置" :visible.sync="dialogBaiduVisible" v-loading="subLoading"
                   :modal-append-to-body="false">
            <el-form :model="baiduform" size="mini" style="padding: 0">
                <el-form-item label="小程序APPKEY" :label-width="formLabelWidth">
                    <el-input v-model="baiduform.appkey" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="小程序APPSECRET" :label-width="formLabelWidth">
                    <el-input v-model="baiduform.appsecret" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="支付APPKEY" :label-width="formLabelWidth">
                    <el-input v-model="baiduform.payappkey" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="支付dealId" :label-width="formLabelWidth">
                    <el-input v-model="baiduform.dealId" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="支付私钥" :label-width="formLabelWidth">
                    <el-input type="textarea" :rows="5" v-model="baiduform.privatekey" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="支付公钥" :label-width="formLabelWidth">
                    <el-input type="textarea" :rows="5" v-model="baiduform.publickey" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="mini" @click="dialogBaiduVisible = false">取 消</el-button>
                <el-button size="mini" type="primary" @click="setBaidu()">确 定</el-button>
            </div>
        </el-dialog>


        <el-dialog title="头条(抖音)小程序登录/支付设置" :visible.sync="dialogToutiaoVisible" v-loading="subLoading" :modal-append-to-body="false">
            <el-form :model="toutiaoform" size="mini" style="padding: 0">
                <el-form-item label="小程序APPID" :label-width="formLabelWidth">
                    <el-input v-model="toutiaoform.appid" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="小程序APPSECRET" :label-width="formLabelWidth">
                    <el-input v-model="toutiaoform.secret" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="支付商户ID" :label-width="formLabelWidth">
                    <el-input v-model="toutiaoform.pay_mid" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="支付APPID" :label-width="formLabelWidth">
                    <el-input v-model="toutiaoform.pay_appid" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="支付密钥" :label-width="formLabelWidth">
                    <el-input type="textarea" :rows="5" v-model="toutiaoform.pay_secret" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="支付宝APPID" :label-width="formLabelWidth">
                    <el-input v-model="toutiaoform.alipay_app_id" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="支付宝公钥" :label-width="formLabelWidth">
                    <el-input v-model="toutiaoform.alipay_public_secret" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="支付宝私钥" :label-width="formLabelWidth">
                    <el-input type="textarea" :rows="5" v-model="toutiaoform.alipay_private_secret" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="mini" @click="dialogToutiaoVisible = false">取 消</el-button>
                <el-button size="mini" type="primary" @click="setToutiao()">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
    import TopNav from "@/components/TopNav";
    import Clipboard from 'clipboard';
    import * as api from "@/api";
    import QrcodeVue from 'qrcode.vue';
    export default {
        name: 'index',
        components: {TopNav,QrcodeVue},
        data() {
            return {
                loading: false,
                subLoading: false,
                dialogWeixinVisible: false,
                dialogAliVisible: false,
                dialogBaiduVisible: false,
                dialogToutiaoVisible: false,
                weixinform: {
                    appid: '',
                    appsecret: '',
                    merchantid: '',
                    merchantsecret: '',
                    cert: '',
                    pem: '',
                },
                aliform: {
                    appid: '',
                    publickey: '',
                    privatekey: '',
                    aeskey: '',
                },
                baiduform: {
                    appkey: '',
                    appsecret: '',
                    payappkey: '',
                    dealId: '',
                    privatekey: '',
                    publickey: '',
                },
                toutiaoform: {
                    appid: '',
                    secret: '',
                    pay_mid: '',
                    pay_appid: '',
                    pay_secret: '',
                    alipay_app_id: '',
                    alipay_public_secret: '',
                    alipay_private_secret: '',
                },
                formLabelWidth: '120px',
                h5Url:'',
                downloadurl:'',
                request:'',
                socket:'',
                uploadFile:'',
                downloadFile:'',
            }
        },
        created() {
            this.getIndex();
        },
        methods: {
            copy() {
                var clipboard = new Clipboard('.tag-read');
                clipboard.on('success', () => {
                    this.$message({
                        message: '复制成功',
                        type: 'success'
                    });
                    clipboard.destroy()
                });
                clipboard.on('error', () => {
                    this.$message({
                        message: '您的浏览器 不支持，请手动复制链接',
                        type: 'error'
                    });
                    clipboard.destroy()
                })
            },
            getIndex() {
                api.post("/shop/admin.app.app/index", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                        this.h5Url = res.url;
                        this.downloadurl = res.downloadurl;
                        this.request = res.request;
                        this.socket = res.socket;
                        this.uploadFile = res.uploadFile;
                        this.downloadFile = res.downloadFile;
                }).catch(() => {

                });
            },
            getWeixin() {
                this.subLoading = true;
                api.post("/shop/admin.app.setting/getOpenSetting", {
                    appToken: this.$store.getters.appToken,
                    type: 'weixin',
                }).then(res => {
                    this.weixinform = res.setting;
                    this.subLoading = false;
                }).catch(() => {
                    this.subLoading = false;
                });
            },

            setWeixin() {
                this.subLoading = true;
                api.post("/shop/admin.app.setting/setOpenSetting", {
                    appToken: this.$store.getters.appToken,
                    type: 'weixin',
                    data: JSON.stringify(this.weixinform),
                }).then(() => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.subLoading = false;
                    this.dialogWeixinVisible = false;
                }).catch(() => {
                    this.dialogWeixinVisible = false;
                    this.subLoading = false;
                });
            },

            getAli() {
                this.subLoading = true;
                api.post("/shop/admin.app.setting/getOpenSetting", {
                    appToken: this.$store.getters.appToken,
                    type: 'ali',
                }).then(res => {
                    this.aliform = res.setting;
                    this.subLoading = false;
                }).catch(() => {
                    this.subLoading = false;
                });
            },

            setAli() {
                this.subLoading = true;
                api.post("/shop/admin.app.setting/setOpenSetting", {
                    appToken: this.$store.getters.appToken,
                    type: 'ali',
                    data: JSON.stringify(this.aliform),
                }).then(() => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.subLoading = false;
                    this.dialogAliVisible = false;
                }).catch(() => {
                    this.dialogAliVisible = false;
                    this.subLoading = false;
                });
            },


            getBaidu() {
                this.subLoading = true;
                api.post("/shop/admin.app.setting/getOpenSetting", {
                    appToken: this.$store.getters.appToken,
                    type: 'baidu',
                }).then(res => {
                    this.baiduform = res.setting;
                    this.subLoading = false;
                }).catch(() => {
                    this.subLoading = false;
                });
            },

            setBaidu() {
                this.subLoading = true;
                api.post("/shop/admin.app.setting/setOpenSetting", {
                    appToken: this.$store.getters.appToken,
                    type: 'baidu',
                    data: JSON.stringify(this.baiduform),
                }).then(() => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.subLoading = false;
                    this.dialogBaiduVisible = false;
                }).catch(() => {
                    this.dialogBaiduVisible = false;
                    this.subLoading = false;
                });
            },

            getToutiao() {
                this.subLoading = true;
                api.post("/shop/admin.app.setting/getOpenSetting", {
                    appToken: this.$store.getters.appToken,
                    type: 'toutiao',
                }).then(res => {
                    this.toutiaoform = res.setting;
                    this.subLoading = false;
                }).catch(() => {
                    this.subLoading = false;
                });
            },

            setToutiao() {
                this.subLoading = true;
                console.log(this.toutiaoform);
                api.post("/shop/admin.app.setting/setOpenSetting", {
                    appToken: this.$store.getters.appToken,
                    type: 'toutiao',
                    data: JSON.stringify(this.toutiaoform),
                }).then(() => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.subLoading = false;
                    this.dialogToutiaoVisible = false;
                }).catch(() => {
                    this.dialogToutiaoVisible = false;
                    this.subLoading = false;
                });
            }

        }
    }
</script>


<style scoped>

    .xcx-type {
        width: 100%;
        height: 260px;
        background: #fff;
        margin: 0 auto;
        box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.1);
        text-align: center;
        font-size: 14px;
        padding: 20px;
        padding-top: 30px;

    }

    .xcx-type .tit {
        line-height: 60px;

    }

    .xcx-type .tit i {
        font-size: 30px;
        color: red;
        margin-right: 10px;
    }

    .xcx-type .qrcode {
        width: 100px;
        height: 100px;
        margin: 0 auto;
    }

    .xcx-type .urlinfo {
        margin-top: 10px;
    }

    .xcx-type .tit2 {
        overflow: hidden;
        padding-top: 10px;
    }

    .xcx-type .tit2 i {
        font-size: 60px;
        color: green;
    }

    .xcx-type p {
        margin-top: 5px;
        line-height: 26px;
    }

    .xcx-type p a {
        color: blue;
    }

    .btn-list {
        margin-top: 20px;
    }
</style>
